import { Component, OnInit, ViewChild } from '@angular/core';
import { MenuController, AlertController } from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import { LoggerServiceProvider } from 'src/providers/logger-service/logger-service';
import { PushService } from 'src/providers/push-service/push-service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';
import { notificationSettingservice } from '../notification-settings/notificationSettingsService';
import * as HttpStatus from 'http-status-codes'
import { Router } from '@angular/router';

@Component({
  selector: 'app-welcome-tour',
  templateUrl: './welcome-tour.page.html',
  styleUrls: ['./welcome-tour.page.scss'],
})
export class WelcomeTourPage {

  screenOne: boolean;
  screenTwo: boolean;
  screenThree: boolean;
  showNotificationScreen: boolean;
  comingfromSettings: boolean;
  comingFromLogin: any;
  errorMsg: any;
  isSuccess: boolean;
  isShowingLoader: boolean;
  tourEnd: any;
  constructor(public menuCtrl: MenuController,private logger: LoggerServiceProvider,
    private utils: UtilService,public router: Router,
    private NotificationSettingservice: notificationSettingservice, private alertCtrl: AlertController, public translate: TranslateService,
    public pushNotiService: PushService, private config: ConfigProvider) {
    this.comingfromSettings = history.state.comingfromSettings;
    this.comingFromLogin = history.state.comingFromLogin;
    this.tourEnd = history.state.tourEnd;
    if (this.comingfromSettings && this.config.userInfo.isNotificationRegistered == false)
      this.showNotificationScreen = true;
      if (this.tourEnd){
        this.screenOne = false;
        this.screenThree = true;
      }else{
        this.screenOne = true;
      }
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad WelcomeTourPage');
  }

  goToNextScreen(currentScreen, button) {
    if (currentScreen == "One") {
      if (button == 'yes') {
        this.screenOne = false;
        if (this.showNotificationScreen)
          this.screenTwo = true;
        else
          this.gotoDashboardTour();
      } else {
        this.skipTourGotoDashboard();
      }
    } else if (currentScreen == "Two") {
      if (button == 'yes') {
        this.notificationPermission();
      } else {
        this.screenTwo = false;
        this.gotoDashboardTour();
      }
    }
    else {
      this.router.navigate(['/dashboard-after-login'], { state: { animate: true, direction: 'forward'} });
      //this.navCtrl.setRoot(DashboardAfterLoginPage, { animate: true, direction: 'forward' });

    }
  }
  skipTourGotoDashboard() {
    localStorage.setItem("welomeTour", 'true');
    let comingFromLoginFlag = { comingFromLogin: true };
    this.router.navigate(['/dashboard-after-login'], { state: { animate: true, direction: 'forward', comingFromLoginFlag: comingFromLoginFlag } });
    //this.navCtrl.setRoot(DashboardAfterLoginPage, comingFromLoginFlag, { animate: true, direction: 'forward' });
    // this.menuCtrl.open();
    // this.events.publish('user:comingFromServiceAppTour',true);
  }
  gotoDashboardTour() {
    localStorage.setItem("welomeTour", 'true');
    let comingFromLoginFlag = { comingFromLogin: true,comingfromWelcomeTour:true };
    this.router.navigate(['/dashboard-after-login'], { state: { animate: true, direction: 'forward', comingFromLoginFlag: comingFromLoginFlag } });
    //this.navCtrl.setRoot(DashboardAfterLoginPage, comingFromLoginFlag, { animate: true, direction: 'forward' });
  }

  notificationPermission() {
    this.utils.showProgressLoader();
    this.pushNotiService.registerDevice().then(
      (result) => {
        this.sendUpdateNotiSettingstoServer();
        this.utils.dissmisProgressLoader();
        this.screenTwo = false;
        this.gotoDashboardTour();
      }, (err) => {
        this.utils.dissmisProgressLoader();
        this.screenTwo = false;
        this.gotoDashboardTour();
      });

  }
  sendUpdateNotiSettingstoServer() {
    if (this.utils.isDeviceOnLine()) {
      this.NotificationSettingservice.updatePushNotificationsetting(true).then((res) => {
        try {
          this.utils.dissmisProgressLoader();
          if (res['responseJSON'] && res['responseJSON']['statusCode'] == HttpStatus.OK) {
            this.errorMsg = res['responseJSON']['userMessage'];
            this.isSuccess = true;
            //this.showDialog("", this.errorMsg);
            this.config.userInfo.isNotificationRegistered = true;

          } else if (res['responseJSON'] && res['responseJSON']['statusCode'] == HttpStatus.ACCEPTED) {
            this.errorMsg = res['responseJSON']['userMessage'];
            //this.showDialog("", this.errorMsg);
            this.config.userInfo.isNotificationRegistered = false;
          } else {
            this.logger.error("PUT USER Notification Settings Update : " + JSON.stringify(res));
            this.errorMsg = this.translate.instant('generic_msgs.server_err');
            this.isSuccess = false;
            this.config.userInfo.isNotificationRegistered = false;
          }
        } catch (e) {
          this.utils.dissmisProgressLoader();
          this.isSuccess = false;
          this.logger.error("PUT USER Notification Settings Update : " + JSON.stringify(e));
          this.errorMsg = this.translate.instant('generic_msgs.server_err');
          this.config.userInfo.isNotificationRegistered = false;
        }
      }, (err) => {
        this.utils.dissmisProgressLoader();
        this.isSuccess = false;
        this.logger.error("PUT USER Notification Settings Update : " + JSON.stringify(err));
        this.errorMsg = this.utils.handleServiceException(err);
        this.config.userInfo.isNotificationRegistered = false;
      });
    } else {
      this.isShowingLoader = false;
      this.isSuccess = false;
      this.errorMsg = this.translate.instant('generic_msgs.network_err');
      this.config.userInfo.isNotificationRegistered = false;
    }
  }
  showDialog(title: string, message: string) {
    let buttons = [{
      text: 'Ok',
      handler: () => {
       // this.navCtrl.pop();
      }
    }];

    let alert = this.alertCtrl.create({
      header: title,
      message: message,
      buttons: buttons,
      backdropDismiss: false,
    }).then((alert) => alert.present());
  }

}
